@import '../variables/15';

@font-face {
    font-family: 'blackjackregular';
    src: url('#{$tocc-site-fonts-dir}/blackjack/black_jack-webfont.eot');
    src: url('#{$tocc-site-fonts-dir}/blackjack/black_jack-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$tocc-site-fonts-dir}/blackjack/black_jack-webfont.woff') format('woff'),
         url('#{$tocc-site-fonts-dir}/blackjack/black_jack-webfont.ttf') format('truetype'),
         url('#{$tocc-site-fonts-dir}/blackjack/black_jack-webfont.svg#blackjackregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@import 'vanilla';